body {
  font-family: "Noto Sans JP", sans-serif;
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

.p-news {
  background-color: #F1F0F0;
  padding-top: 0.875rem;
  padding-bottom: 3.5rem;
}
@media screen and (min-width: 768px) {
  .p-news {
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
}

.p-news__header {
  text-align: center;
  display: inline-block;
}

.p-news__textblock {
  margin-top: 1.875rem;
}

.p-news__text {
  text-align: center;
}

.p-news__text-pc {
  display: none;
}
@media screen and (min-width: 768px) {
  .p-news__text-pc {
    display: block;
  }
}

.p-news-card {
  margin-top: 1.875rem;
}
@media screen and (min-width: 768px) {
  .p-news-card {
    display: flex;
    justify-content: center;
    margin-top: 2.5rem;
  }
}

.p-card-news__body {
  padding-top: 1.25rem;
  padding-right: 1.25rem;
  padding-bottom: 1.5625rem;
  padding-left: 0.625rem;
  border-left: 50px solid #AF9E97;
}

.p-card-news__body:hover {
  background-color: #AF9E97;
  border-left: 50px solid #F7F7F7;
}

.p-card-news__body:hover .c-card__title {
  color: #F7F7F7;
}

.p-card-news__body:hover .c-card__text {
  color: #F7F7F7;
}

.p-news__button-block {
  text-align: center;
  margin-top: 1.875rem;
}
@media screen and (min-width: 768px) {
  .p-news__button-block {
    margin-top: 2.5rem;
  }
}

.p-news__button {
  background-color: #4D4D4D;
  color: #F7F7F7;
}

.p-news__button:hover {
  background-color: #F7F7F7;
  color: #4D4D4D;
  border: 1px solid #4D4D4D;
  margin: -1px;
}